export default {
	data() {
		return {
			userLogin:{},
			sex:''
		};
	},
	onLoad() {
		this.userLogin=uni.getStorageSync('userLogin')
		this.sex=this.userLogin.sex
	},
	methods:{
		changeSex(){
			uni.setStorageSync('userLogin',this.userLogin)
			uni.navigateBack({
				delta:1
			})
		},
		changeRadio(e){
			this.userLogin.sex=e.detail.value
		}
	}
};
